import { createStore } from 'redux'
import Web3 from 'web3';
import config from '../contract/index';
import { toast } from 'react-toastify';

const _initialState = {
    account: "",
    purchaseAllowed: false,
    
    name: "Swapsicle",
    imageUrl: "",
    introTitle: "Welcome to our presale",
    introDescription: "On purchase pPOPS tokens will be immediately sent to your wallet. When the DEX is live you will be able to swap your pPOPS token 1 to 1 with our POPS token.",
    purchasedTitle: "Congratulations!",
    purchasedDescription: "Purchase complete. Your pPOP tokens will be immediately sent to your wallet.",
    whitepaperUrl: "#",
    investToken: "USDC.e",
    investTokenAmount: "0",
    returnToken: "pPOP",
    returnTokenAmout: "0",
    minInvest: "$ 100",
    maxInvest: "$ 20,000",
    transaction: ""
    
}

const init = (init) => {
    return init;
}

const globalWeb3 = new Web3(config.mainNetUrl);
const provider = Web3.providers.HttpProvider(config.mainNetUrl);
const web3 = new Web3(Web3.givenProvider || provider);

const contract = new web3.eth.Contract(config.contractAbi, config.contractAddress);
const usdt = new web3.eth.Contract(config.usdtAbi, config.coinAddress);

const calcTokenAmount = async (state, investTokenAmount) => {
    try {        
        var coinDecimals = await usdt.methods.decimals().call();        
        var amount = new web3.utils.toBN(web3.utils.toWei(Number(investTokenAmount).toString(), 'ether'));
        amount = amount.mul(new web3.utils.toBN(Math.pow(10, coinDecimals).toString()));
        amount = amount.div(new web3.utils.toBN(Math.pow(10, 18).toString()));
        console.log(amount);
        var value = await contract.methods.getUserRewardAmount(0, 0, amount, state.account).call();
        // var value = await contract.methods.getUserRewardAmount(0, 1, amount, state.account).call();
        
        var tokenAmount = web3.utils.fromWei(value[0], 'ether');
        console.log(tokenAmount);
        store.dispatch({ type: "RETURN_DATA", payload: { investTokenAmount: investTokenAmount, returnTokenAmount: tokenAmount, returnCoinAmount: value[1] } });
    } catch (e) {
        console.log("error: ", e);
        store.dispatch({ type: "RETURN_DATA", payload: { returnTokenAmount: 0, returnCoinAmount: amount } });
    }

}

const buy = async (state, investTokenAmount) => {
    if (!state.account) {
        alertMsg("Please connect metamask!");
        return;
    }  

    try {
        var coinDecimals = await usdt.methods.decimals().call();
        var amount = new web3.utils.toBN(investTokenAmount);
        amount = amount.mul(new web3.utils.toBN(Math.pow(10, coinDecimals).toString()));
        amount = amount.sub(new web3.utils.toBN(state.returnCoinAmount));
        await usdt.methods.approve(config.contractAddress, amount).send({ from: state.account });
        await contract.methods.invest(0, 0, amount).send({ from: state.account, gas: 3000000 });
        // await contract.methods.invest(0, 1, amount).send({ from: state.account, gas: 3000000 });
    } catch (e) {
        console.log("error: ", e);
        store.dispatch({ type: "RETURN_DATA", payload: { returnTokenAmount: 0, returnCoinAmount: amount } });
    }
}

const reducer = (state = init(_initialState), action) => {
    switch (action.type) {
        case 'CONNECT_WALLET':
            if (!checkNetwork(state.chainId)) { 
                changeNetwork();
                return state;
            } 
            web3.eth.getAccounts((err, accounts) => {
                store.dispatch({
                    type: 'RETURN_DATA',
                    payload: { account: accounts[0], purchaseAllowed: true }
                });
            })
            break;

        case 'GET_TOKEN_AMOUNT':
            if (!checkNetwork(state.chainId)) { 
                changeNetwork();
                return state;
            } 
            calcTokenAmount(state, action.payload.investTokenAmount);
            break;

        case 'BUY_TOKEN':
            if (!checkNetwork(state.chainId)) { 
                changeNetwork();
                return state;
            } 
            buy(state, action.payload.investTokenAmount);
            break;
        case 'RETURN_DATA':
            return Object.assign({}, state, action.payload);
            break;

        default:
            break;
    }
    return state;
}

const alertMsg = (msg) => {
    toast.info(msg, {
        position: "top-center",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
}

const checkNetwork = (chainId) => {
    if (web3.utils.toHex(chainId) !== web3.utils.toHex(config.chainId)) {
        alertMsg("Change network to Avalanche C Chain!");
        return false;
    } else {
        return true;
    }
}

const changeNetwork = async () => {
    try {
        await window.ethereum.request({
          method: 'wallet_switchEthereumChain',
          params: [{ chainId: config.chainId }],
        });
      } catch (switchError) {
        // This error code indicates that the chain has not been added to MetaMask.
        if (switchError.code === 4902) {
          try {
            await window.ethereum.request({
              method: 'wallet_addEthereumChain',
              params: [
                {
                  chainId: config.chainId,
                  chainName: 'Avalanche',
                  rpcUrls: [config.mainNetUrl] /* ... */,
                },
              ],
            });
          } catch (addError) {
          }
        }
      }
}


if (window.ethereum) {
    window.ethereum.on('accountsChanged', function (accounts) {
        store.dispatch({
            type: "RETURN_DATA",
            payload: { account: accounts[0] }
        });
    })
    window.ethereum.on('chainChanged', function (chainId) {
        checkNetwork(chainId);
        store.dispatch({
            type: "RETURN_DATA",
            payload: { chainId: chainId }
        });
    });
    web3.eth.getChainId().then((chainId) => {
        checkNetwork(chainId);
        store.dispatch({
            type: "RETURN_DATA",
            payload: { chainId: chainId }
        });
    })
}

const store = createStore(reducer);
export default store